
Template.__checkName("post_share");
Template["post_share"] = new Template("Template.post_share", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-share ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.A({
    href: "#",
    "class": "share-link action",
    title: function() {
      return Spacebars.mustache(view.lookup("_"), "share");
    }
  }, "\n      ", Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "share"));
  }), "\n    "), "\n    ", HTML.DIV({
    "class": "share-options hidden"
  }, "\n      ", HTML.A({
    "class": "share-option-facebook",
    href: function() {
      return [ "https://www.facebook.com/sharer/sharer.php?u=", Spacebars.mustache(Spacebars.dot(view.lookup("."), "getShareableLink")) ];
    },
    target: "_blank"
  }, Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "facebook"));
  })), "\n      ", HTML.A({
    "class": "share-option-twitter",
    href: function() {
      return [ "//twitter.com/intent/tweet?text=", Spacebars.mustache(view.lookup("encodedTitle")), "&url=", Spacebars.mustache(Spacebars.dot(view.lookup("."), "getShareableLink")), "&", Spacebars.mustache(view.lookup("viaTwitter")) ];
    },
    target: "_blank"
  }, Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "twitter"));
  })), "\n      ", HTML.A({
    "class": "share-option-linkedin",
    href: function() {
      return [ "//www.linkedin.com/shareArticle?mini=true&url=", Spacebars.mustache(Spacebars.dot(view.lookup("."), "getShareableLink")), HTML.CharRef({
        html: "&amp;",
        str: "&"
      }), "summary=", Spacebars.mustache(view.lookup("encodedTitle")) ];
    },
    target: "_blank"
  }, Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "linkedin"));
  })), "\n      ", HTML.A({
    "class": "share-option-google",
    href: function() {
      return [ "https://plus.google.com/share?url=", Spacebars.mustache(Spacebars.dot(view.lookup("."), "getShareableLink")) ];
    },
    target: "_blank"
  }, Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "googleplus"));
  })), "\n    "), "\n  ");
}));
